<template>
  <div class="position-wrap">
      <ul class="nav nav-tabs" id="storageTab">
        <li><a href="#possess" class="block" data-toggle="tab">持仓查询</a></li>
        <li><a href="#sold" class="block" data-toggle="tab">平仓查询</a></li>
      </ul>
      <div class="tab-content ">
        <div class="tab-pane" id="possess">
            <table>
                <tbody>
                    <tr>
                        <th>品种</th>
                        <th>数量</th>
                        <th>盈亏</th>
                    </tr>
                    <tr>
                        <td>January</td>
                        <td>100</td>
                        <td>$100</td>
                    </tr>
                    <tr>
                        <td>January</td>
                        <td>100</td>
                        <td>$100</td>
                    </tr>
                    <tr>
                        <td>January</td>
                        <td>100</td>
                        <td>$100</td>
                    </tr>
                    <tr>
                        <td>January</td>
                        <td>100</td>
                        <td>$100</td>
                    </tr>
                </tbody>
            </table>
        </div>
        <div class="tab-pane" id="sold">
            <table>
                <tbody>
                    <tr>
                        <th>品种</th>
                        <th>数量</th>
                        <th>盈亏</th>
                    </tr>
                    <tr>
                        <td>January</td>
                        <td>100</td>
                        <td>$100</td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      msg: ''
    }
  },
  methods: {
    onTabClick () {
      var btn = document.querySelectorAll('.position-wrap .nav li')
      for (let i = 0; i < btn.length; i++) {
        btn[i].className = ''
      }
    }
  }
}
</script>

<style lang="less" scoped>
// 持仓
.position-wrap {
  position: relative;
  width: 100%;
  height: auto;
  color: #333;
  font-size: .14rem;

  .nav-tabs {
    width: 100%;
    display: table;
    height: .38rem;
    line-height: .38rem;
    text-align: center;

    li {
      display: table-cell;
      background-color: #fff;
      color: #666;

      a {
        color: #666;
      }

      &:hover,
      &.active {
        background-color: darken(#fff, 10%);
        color: #333;

        a {
          color: #333;
        }
      }
    }
  }

  .tab-content {
    width: 100%;

    table {
      width: 100%;
      text-align: left;

      tr {
        line-height: 2;
        border-bottom: .01rem solid #e5e5e5;

        &:last-child {
          border: none;
        }
      }

      th {
        background-color: darken(#fff, 5%);
      }
    }
  }
}
</style>




