import Vue from 'vue'
import Router from 'vue-router'
import deal from '@/components/deal'
import account from '@/components/account'
import position from '@/components/position'

Vue.use(Router)

const routes = [
  {
    path: '/deal',
    component: deal
  },
  {
    path: '/account',
    component: account
  },
  {
    path: '/position',
    component: position
  },
  {
    path: '/',
    redirect: '/deal'
  }
]

var router = new Router({
  routes
})

export default router



