<template>
  <div>
    <ul class="account-list">
      <li class="padding-wrap"><a href="#" class="block">资金</a></li>
      <li class="padding-wrap"><a href="#" class="block">入金</a></li>
      <li class="padding-wrap"><a href="#" class="block">出金</a></li>
    </ul>
  </div>
</template>

<script>
export default {
  data () {
    return {
      msg: ''
    }
  }
}
</script>

<style lang="less" scoped>
// 账户列表
.account-list {
  position: relative;
  width: 100%;
  height: auto;
  color: #333;
  font-size: .16rem;
  text-align: left;

  a {
    color: #333;
    width: 100%;
    height: 100%;
  }

  li {
    height: .48rem;
    line-height: .48rem;
    border-bottom: .01rem solid #e5e5e5;
    position: relative;

    &:active {
      background-color: darken(#fff, 5%);

      &:before {
        border-left-color: darken(#b9b9b9, 20%);
      }

      &:after {
        border-left-color: darken(#fff, 5%);
      }
    }

    &:before,
    &:after {
      content: '';
      width: 0;
      height: 0;
      position: absolute;
      border: .12rem solid transparent;
      top: .13rem;
    }

    &:before {
      border-left-color: #b9b9b9;
      right: 0;
    }

    &:after {
      border-left-color: #fff;
      right: .03rem;
    }
  }
}
</style>



