<template>
  <div class="product-list padding-wrap">
    <p class="title">商品列表</p>
      <ul>
        <li class="padding-wrap">
          <a href="stock.html" class="block">
            <div class="product-name text-ellipsis">商品1</div>
            <div class="product-value text-ellipsis">525625256252525625256252</div>
          </a>
        </li>
        <li class="padding-wrap">
          <a href="stock.html" class="block">
            <div class="product-name text-ellipsis">商品2</div>
            <div class="product-value text-ellipsis">5520263</div>
          </a>
        </li>
        <li class="padding-wrap">
          <a href="stock.html" class="block">
            <div class="product-name text-ellipsis">商品3</div>
            <div class="product-value text-ellipsis">5552</div>
          </a>
        </li>
    </ul>
  </div>
</template>

<script>
export default {
  data () {
    return {
      msg: ''
    }
  }
}
</script>

<style lang="less" scoped>
// 商品列表
.product-list {
  position: relative;
  width: 100%;
  height: auto;
  color: #333;
  text-align: left;

  a {
    color: #333;
    width: 100%;
    height: 100%;
  }

  .title {
    font-size: .2rem;
    line-height: 1;
    font-weight: bold;
    margin: .15rem 0;
  }

  ul {
    width: 100%;
    font-size: .16rem;
    border: .01rem solid #e5e5e5;
    border-bottom: none;

    li {
      width: 100%;
      height: .42rem;
      line-height: .42rem;
      border-bottom: .01rem solid #e5e5e5;
      background-color: #f5f5f5;
      position: relative;

      &:active {
        background-color: darken(#f5f5f5, 20%);

        &:before {
          border-left-color: darken(#b9b9b9, 20%);
        }

        &:after {
          border-left-color: darken(#f5f5f5, 20%);
        }
      }

      &:before,
      &:after {
        content: '';
        width: 0;
        height: 0;
        position: absolute;
        border: .12rem solid transparent;
        top: .1rem;
      }

      &:before {
        border-left-color: #b9b9b9;
        right: 0;
      }

      &:after {
        border-left-color: #f5f5f5;
        right: .03rem;
      }
    }
  }

  .product-name {
    float: left;
    width: 2rem;
  }

  .product-value {
    float: right;
    width: 1rem;
    padding-right: .05rem;
  }
}
</style>


