<template>
  <div id="app">
    <div class="container">
      <header class="header">
        <div class="first-title">交易中心</div>
      </header>
      <div class="content">
        <div class="fund-invest padding-wrap">
          <div class="fund">资金：300元</div>
          <div class="invest">
            <div class="btn">入金</div>
          </div>
        </div>
        <router-view></router-view>
      </div>
      <footer class="footer">
        <footer class="footer">
          <ul class="nav-tabs">
            <li v-for="(btnName, index) in footerBtn" @click="onTabClick(index)">
              <router-link :to="'/' + btnName.id">{{btnName.name}}</router-link>
            </li>
          </ul>
        </footer>
      </footer>
    </div>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      footerBtn: [
        {id: 'deal', name: '交易'},
        {id: 'account', name: '账户'},
        {id: 'position', name: '持仓'}
      ]
    }
  },
  methods: {
    onTabClick (index) {
      var btn = document.querySelectorAll('.footer li')
      for (let i = 0; i < btn.length; i++) {
        btn[i].className = ''
      }
      btn[index].className = 'active'
    }
  }
}
</script>

<style lang="less">
@import './style/common.less';
</style>

<style lang="less">
@import './style/base.less';
@import './style/mixins/mixins.less';

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

.border-left {
  .border-left(0, 0);
}

.border-right {
  .border-right(0, 0);
}

.border-top {
  .border-top(0, 0);
}

.border-bottom {
  .border-bottom(0, 0);
}

.ratio-1x {
  .border-left:after,
  .border-right:after {
    -webkit-transform: scaleX(1);
    transform: scaleY(1);
  }

  .border-top:after,
  .border-bottom:after {
    -webkit-transform: scaleY(1);
    transform: scaleY(1);
  }
}

.ratio-2x {
  .border-left:after,
  .border-right:after {
    -webkit-transform: scaleX(.5);
    transform: scaleY(.5);
  }

  .border-top:after,
  .border-bottom:after {
    -webkit-transform: scaleY(.5);
    transform: scaleY(.5);
  }
}

.ratio-3x {
  .border-left:after,
  .border-right:after {
    -webkit-transform: scaleX(.3);
    transform: scaleY(.3);
  }

  .border-top:after,
  .border-bottom:after {
    -webkit-transform: scaleY(.3);
    transform: scaleY(.3);
  }
}

.pull-left {
  float: left;
}

.pull-right {
  float: right;
}

.right {
  text-align: right !important;
}

.left {
  text-align: left !important;
}

.center {
  text-align: center !important;
}

.vcenter {
  display: block;
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: auto;
  text-align: center;
  .translate(0, -50%);
}

.block {
  display: block;
}

.hidden {
  display: none !important;
}

.invisible {
  visibility: hidden !important;
}

.text-ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.content {
  position: relative;
  margin-bottom: @footer-height;
  padding-bottom: .15rem;
}

.padding-wrap {
  padding: 0 .15rem;
}

// 按钮
.btn {
  width: 100%;
  height: .32rem;
  color: @main-white;
  background-color: @btn-color;
  line-height: .32rem;
  text-align: center;
  .border-radius();
  cursor: pointer;

  &:active {
    background-color: darken(@btn-color, 10%);
  }
}

// 资金-投资
.fund-invest {
  position: relative;
  width: 100%;
  height: .45rem;
  background-color: @main-blue;
  color: @main-white;
  line-height: .45rem;

  .fund {
    float: left;
    font-size: .14rem;
  }

  .invest {
    float: right;
    font-size: .12rem;
    width: .5rem;

    .btn {
      background-color: #d65452;
      margin-top: .06rem;

      &:active {
        background-color: darken(#d65452, 10%);
      }
    }
  }
}

// header 
.header {
  width: 100%;
  position: relative;
  text-align: center;
  color: @main-white;

  .first-title {
    height: @first-title-height;
    background-color: lighten(@main-black, 15%);
    line-height: @first-title-height;
    font-size: .16rem;
  }

  .sub-title {
    height: @sub-title-height;
    background-color: lighten(@main-black, 18%);
    line-height: @sub-title-height;
    font-size: .18rem;
  }
}

// footer
.footer {
  width: 100%;
  height: @footer-height;
  position: fixed;
  bottom: 0;
  font-size: .14rem;
  background-color: lighten(@main-black, 20%);

  a {
    display: block;
    width: 100%;
    height: 100%;
  }

  .nav-tabs {
    width: 100%;
    height: 100%;
    display: table;

    a {
      background-color: @footer-bg;
      text-align: center;
      line-height: @footer-height;
      color: darken(@main-white, 15%);
    }

    li {
      display: table-cell;
      height: 100%;

      &:hover,
      &.active {

        a {
          background-color: darken(@footer-bg, 10%);
          color: @main-white;
        }
      }
    }
  }
}

// footer-btn
.btn-wrap {
  display: table;
  width: 100%;
  margin-top: .18rem;
  padding-left: .15rem;

  li {
    display: table-cell;
    padding-right: .15rem;
  }
}

// 商品详情
.product {
  width: 100%;
  color: @main-white;
  text-align: left;
  border-bottom: 0.01rem solid lighten(@main-black, 20%);

  th {
    background-color: lighten(@main-black, 20%);
    font-size: .12rem;
    height: .3rem;
    color: lighten(@main-black, 50%);
  }

  td {
    font-size: .14rem;
    height: .5rem;

    .update {
      color: lighten(@main-black, 45%);
      font-size: .12rem;
    }

    &.data {
      color: #14B143;
    }
  }
}

// K线图
.k-line {
  width: 100%;
  height: 3rem;
}

// 表单
.form {
  padding: .3rem .15rem;

  .input-wrap {
    width: 100%;
    height: .35rem;
    line-height: .35rem;
    color: darken(@main-white, 20%);
    font-size: .14rem;
    margin-bottom: .12rem;
    position: relative;

    .text {
      float: left;
      width: 26%;
      text-align: right;
      padding-right: .1rem;
      height: .35rem;
      line-height: .35rem;
    }

    input,
    select {
      float: left;
      width: 52%;
      height: .35rem;
      line-height: .35rem;
      background-color: lighten(@main-black, 20%);
      padding: 0 .15rem;
      color: lighten(@main-black, 75%);
    }

    .tips {
      float: left;
    }

    input[type=checkbox] {
      position: absolute;
      top: 0;
      left: 4%;
      width: .14rem;
    }
  }
}

// 买入
.buy-form {

  .tips {
    width: 100%;
    text-align: center;

    span {
      margin-right: .2rem;
    }
  }
}

// 卖出
.sell-form {

  .tips {
    color: #9d4b62;
    width: 20%;
    overflow: hidden;
    padding-left: .1rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

// PC 兼容
@media screen and ( min-width: 1025px) {
  .container {
    width: @max-width;
    margin-left: auto;
    margin-right: auto;
  }

  .footer {
    width: @max-width;
  }
}
</style>



